import React from 'react';
import './App.css';

/* Router */
import { BrowserRouter, Route, NavLink } from 'react-router-dom';
import Home from './components/Dashboard';
import CreateApi from './components/AddApi';
import PostApi from './components/ReadApi';
import EditApi from './components/UpdateApi';

/* Design */
import { Layout, Menu, Avatar, Typography } from 'antd';
import {
  FormOutlined,
  DashboardOutlined ,
  CheckCircleOutlined,
  TeamOutlined,
  UserOutlined,
  SmileOutlined,
  BarsOutlined,
} from '@ant-design/icons';

const { Header, Content, Footer, Sider } = Layout;
const { Title } = Typography;
const { SubMenu } = Menu;

/* Component */
class App extends React.Component {
  state = {
    collapsed: false,
  };

  onCollapse = collapsed => {
    console.log(collapsed);
    this.setState({ collapsed });
  };

  render() {
    return (
      <BrowserRouter>

      <Layout style={{ minHeight: '100vh' }}>
        <Sider collapsible collapsed={this.state.collapsed} onCollapse={this.onCollapse} >
         <div className="logo" />

          <Menu theme="dark" defaultSelectedKeys={['2']} mode="inline">
            <Menu.Item key="1"> <Avatar style={{float:"left"}} src="icon-atop.png"/> 
            </Menu.Item> 
            
            <Menu.Item key="2" icon={<DashboardOutlined />} >
              Dashboard
            </Menu.Item>

            <SubMenu key="sub1" icon={<FormOutlined  />} title="Form">
              <Menu.Item key="3" icon={<SmileOutlined />}> Create
              <NavLink className="nav-link" activeStyle={{ fontWeight: 'bold' }} to="/create" exact></NavLink>
              </Menu.Item>
              <Menu.Item key="4"icon={<SmileOutlined />}> Edit
              <NavLink className="nav-link" activeStyle={{ fontWeight: 'bold' }} to="/edit/:id" exact></NavLink>
              </Menu.Item>
            </SubMenu>

            <SubMenu key="sub2" icon={<UserOutlined />} title="Account">
              <Menu.Item key="5" icon={<SmileOutlined />}>Account Center</Menu.Item>
              <Menu.Item key="6"icon={<SmileOutlined />}>Account Setting</Menu.Item>
            </SubMenu>

            <SubMenu key="sub3" icon={<TeamOutlined />} title="Team">
              <Menu.Item key="7" icon={<SmileOutlined />}>Team 1</Menu.Item>
              <Menu.Item key="8" icon={<SmileOutlined />}>Team 2</Menu.Item>
            </SubMenu>

            <SubMenu key="sub4" icon={<CheckCircleOutlined />} title="Result">
              <Menu.Item key="9" icon={<SmileOutlined />}>IDLE</Menu.Item>
              <Menu.Item key="10" icon={<SmileOutlined />}>Run Time</Menu.Item>
              <Menu.Item key="11" icon={<SmileOutlined />}>Down Time</Menu.Item>
            </SubMenu>

            <SubMenu key="sub5" icon={<BarsOutlined />} title="List">
              <Menu.Item key="11" icon={<SmileOutlined />}>Search List
              <NavLink className="nav-link" activeStyle={{ fontWeight: 'bold' }} to="/post" exact></NavLink>
              </Menu.Item>
              <Menu.Item key="12" icon={<SmileOutlined />}>Search Table</Menu.Item>
              <Menu.Item key="13" icon={<SmileOutlined />}>Card List</Menu.Item>
            </SubMenu>

          </Menu>
        </Sider>

      <Layout className="site-layout">
          
        <Header className="site-layout-background" style={{   padding:10}} > 
          <Avatar style={{float:"right"}} icon={<UserOutlined />}/>
          <Title style={{ color: "black" }} level={3}>ATOP Agricultrual Trace System</Title>
        </Header>

        <Content style={{ margin: '0 16px' }}>

            <div className="" style={{ padding: 24, minHeight: 360 }}>
                <Route path="/" exact component={Home}></Route>
                <Route path="/create" exact component={CreateApi}></Route>
                <Route path="/post" exact component={PostApi}></Route>
                <Route path="/edit/:id" exact component={EditApi}></Route>
            </div>
          </Content>

        <Footer style={{ textAlign: 'center' }}>Atop Technologies © Copyright 2020</Footer>
      
      </Layout>
      </Layout>
      </BrowserRouter>
    );
  }
}

export default App;


