import React, { useEffect } from 'react';
import { useParams } from "react-router-dom";
import { Link } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import axios from 'axios';

const EditApi = (props) => {
    const { id } = useParams();
    const { register, handleSubmit, errors, setValue } = useForm();

    useEffect(() => {
        axios.get(`http://localhost:8000/users/${id}`).then(result => {
            setValue("id", result.user.id);
            setValue("name", result.user.name);
            setValue("jabatan", result.user.jabatan);
            setValue("username", result.user.username);
        })
    }, [id]);

    const onSubmit = user => {
        axios.put(`http://localhost:8000/users/${id}`, user).then(result => {
            props.history.push("/");
        })
    };

    return (
        <div className="card">
            <div className="card-body">
                <h5 className="card-title">Update</h5>
                <div className="card-text">
                    <form onSubmit={handleSubmit(onSubmit)}>
                        <div className="form-group">
                            <label>ID</label>
                            <input type="text" readOnly className="form-control" name="id" ref={register({ required: true })} />
                        </div>
                        <div className="form-group">
                            <label>Name</label>
                            <input type="text" className="form-control" name="name" ref={register({ required: true })} />
                            <small className="form-text text-danger">{errors.name && 'invalid'}</small>
                        </div>
                        <div className="form-group">
                            <label>Jabatan</label>
                            <input type="text" className="form-control" name="jabatan" ref={register({ required: true })} />
                            <small className="form-text text-danger">{errors.jabatan && 'invalid'}</small>
                        </div>
                        <div className="form-group">
                            <label>Username</label>
                            <input type="text" className="form-control" name="username" ref={register({ required: true })} />
                            <small className="form-text text-danger">{errors.username && 'invalid'}</small>
                        </div>

                        <Link to="/" className="btn btn-primary">
                            <i className="fa fa-arrow-left"></i> Cancelar
                        </Link>
                        &nbsp;
                        <button type="submit" className="btn btn-primary">Salvar <i className="fa fa-save"></i></button>
                    </form>
                </div>
            </div>
        </div>
    );
}

export default EditApi;