import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';

const PostApi = () => {
    const [users, setUsers] = useState([]);

useEffect(() => {
        axios.get('/todos').then(result => console.log(result.data))//(result => {const users = result.data;}) ////(result => {const users = result.data;}) 
        setUsers(users);
      
    },[]);

    return (
        <table className="table">
            <thead>
                <tr>
                    <th className="text-center">Name</th>
                    <th className="text-center">jabatan</th>
                    <th className="text-center">Username</th>
                    <th className="text-center">Action</th>
                </tr>
            </thead>
              <tbody>
                 {
                    users.map(user => (
                        <tr key={user}>
                            <td>{user.name}</td>
                            <td className="text-center">{user.jabatan}</td>
                            
                            <td className="text-center">{user.username}</td>
                            <td className="text-center">
                                <div className="btn-group">
                                    <Link to={`/edit/${user.id}`} className="btn btn-primary">
                                        <i className="fa fa-edit"></i>
                                    </Link>
                                    <Link to={`/details/${user.id}`} className="btn btn-secondary">
                                        <i className="fa fa-eye"></i>
                                    </Link>
                                    <Link to={`/delete/${user.id}`} className="btn btn-danger">
                                        <i className="fa fa-trash"></i>
                                    </Link>
                                </div>
                            </td>
                        </tr>
                    ))
                }
            </tbody>
        </table>
    );
}

export default PostApi;