import React from 'react';
import { Chart, Line} from 'bizcharts';
import { Card } from 'antd';

const data = [
    { month: "2015-01-01", V: 84.0 },
    { month: "2015-02-01", V: 14.9 },
    { month: "2015-03-01", V: 17.0 },
    { month: "2015-04-01", V: 20.2 },
    { month: "2015-05-01", V: 55.6 },
    { month: "2015-06-01", V: 56.7 },
    { month: "2015-07-01", V: 30.6 },
    { month: "2015-08-01", V: 63.2 },
    { month: "2015-09-01", V: 24.6 },
    { month: "2015-10-01", V: 14.0 },
    { month: "2015-11-01", V: 9.4 },
    { month: "2015-12-01", V: 7.3 }
  ];
  

function Home() {
    return (
   
        <Card title="Real-Time Monitoring Machine Activity" style={{ width: 900 }}>
            <Chart scale={{value: {min: 0}}} padding={[10,20,50,40]} autoFit height={500} data={data} >
                <Line
                shape="smooth"
                position="month*V"
                color="l (270) 0:rgba(255, 146, 255, 1) .5:rgba(100, 268, 255, 1) 1:rgba(215, 0, 255, 1)"
                />
            </Chart>
        </Card>
                

    );
}
  
  
  
  export default Home;