import React from 'react';
import { useForm } from 'react-hook-form';
import axios from 'axios';

const CreateApi = (props) => {
    const { register, handleSubmit, errors } = useForm();
    const onSubmit = data => {
        axios.post('http://localhost:8000/api/post', data).then(result => {
            props.history.push("/");
        })
    };

    return (
        <div className="card">
            <div className="card-body">
                <h5 className="card-title">Add</h5>
                <div className="card-text">
                    <form onSubmit={handleSubmit(onSubmit)}>
                        <div className="form-group">
                            <label>Name</label>
                            <input type="text" className="form-control" name="name" ref={register({ required: true })} />
                            <small className="form-text text-danger">{errors.name && 'invalid'}</small>
                        </div>
                        <div className="form-group">
                            <label>Jabatan</label>
                            <input type="text" className="form-control" name="jabatan" ref={register({ required: true })} />
                            <small className="form-text text-danger">{errors.jabatan && 'invalid'}</small>
                        </div>
                        <div className="form-group">
                            <label>Username</label>
                            <input type="text" className="form-control" name="username" ref={register({ required: true })} />
                            <small className="form-text text-danger">{errors.username && 'invalid'}</small>
                        </div>
                        <button type="submit" className="btn btn-primary">Submit</button>
                    </form>
                </div>
            </div>
        </div>
    );
}

export default CreateApi;